:root {
  font-size: 16px;
  font-family: 'Open Sans';
  --text-primary: #bb6666;
  --text-secondary: #ececec;
  --bg-primary: #23232e;
  --bg-secondary: #141414;
}

body {
  color: black;
  background-color: white;
  margin: 0;
  padding: 0;
}

body::-webkit-scrollbar {
  width: .5rem;
}

body::-webkit-scrollbar-thumb {
  background: #0a3991;
}

main {
  margin-left: 5rem;
  padding: 1rem;
}

.navbar {
  position: fixed;
  background-color: var(--bg-primary);
  transition: width 100ms ease;
}

.navbar-nav {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
}

.nav-item {
  width: 100%;
}

/* .nav-item:last-child {
  margin-top: auto;
} */

.nav-link {
  display: flex;
  align-items: center;
  color: var(--text-primary);
  text-decoration: none;
  height: 5rem;
  filter: opacity(0.7);
  transition: var(--transition-speed);
  padding: .5rem;
}

.nav-link:hover {
  filter: opacity(1.0);
  transition: var(--transition-speed);
  background: var(--bg-secondary);
}

.link-text {
  display: none;
  margin-left: 1rem;
}

.logo {
  font-weight: bold;
  text-transform: uppercase;
  margin-bottom: 1rem;
  text-align: center;
  color: var(--text-secondary);
  background: var(--bg-secondary);
  font-size: 1.5rem;
  letter-spacing: 0.3ch;
  width: 100%;
}

.logo img {
  transform: rotate(0deg);
  transition: transform var(--transition-speed);
}

.navbar:hover .logo img {
  transform: rotate(180deg);
  /* transition: transform var(--transition-speed); */
}

.nav-link img {

}

/* small screens */
@media only screen and (max-width: 600px) {
  .navbar {
    bottom: 0;
    width: 100vw;
    height: 5rem;
  }

  .logo {
    display: none;
  }

  .navbar-nav {
    flex-direction: row;
  }

  .nav-link {
    justify-content: center;
  }

  main {
    margin: 0;
  }
}

/* large screens */
@media only screen and (min-width: 600px) {
  
  .navbar {
    top: 0;
    width: 5rem;
    height: 100vh;
  }

  .navbar:hover {
    width: 16rem;
  }

  .navbar:hover .link-text{
    display: inline;
    transition: opacity var(--transition-speed);
  }
}